@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
  font-size: medium;
}

.main-container {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  display: grid;
  grid-template-columns: max-content 1fr;
  grid-template-rows: max-content 1fr;
}

header {
  width: 100vw;
  height: 38px;
  position: sticky;
  background-color: rgb(48, 17, 52);
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
}

.logo-container{
  height: 38px;
  width: 120px;
  cursor: pointer;
}

.floating-icon:hover,
.logo-container:hover {
  background-color: rgb(78, 43, 82);
  cursor: pointer;
}

.slack-logo {
  position: absolute;
  top: 8px;
  left: 12px;
  width: 90px;
  cursor: pointer;
}

.side-bar {
  height: 100%;
  width: 260px;
  border: 1px solid rgb(78, 43, 82);
  background-color: rgb(58, 19, 62);
  color: rgb(205, 196, 206);
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
  display: flex;
  flex-direction: column;
}

.section-title {
  display: flex;
  align-items: center;
  margin: 10px 10px 5px 15px;
}

.floating-icon {
  padding: 5px;
  width: 15px;
  border-radius: 4px;
}

.channel-container,
.user-container {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 5px 35px;
}

.channel-container:hover, 
.user-container:hover {
  background-color: rgb(48, 17, 52);
  cursor: pointer;
}

.title-text {
  margin-left: 10px;
}

.channel-icon {
  width: 10px;
}

.side-bar-direct-messages {
  margin-top: 15px;
}

.channel,
.user {
  margin-left: 10px;
}


.selected {
  background-color: rgb(44, 100, 158);
  color: white;
}

.selected:hover {
  background-color: rgb(44, 100, 158);
}

.x-icon {
  color: rgb(205, 196, 206);
  position: absolute;
  left: 240px;
  display: none;
}

.x-icon:hover {
  color: rgb(255, 255, 255);
}

.user-container:hover > .x-icon {
  display: block;
}

.user-indicator {
  margin-left: 10px;
  font-weight: 300;
  color: rgb(169, 161, 170);
}

.profile-icon {
  background-color: rgb(164, 165, 83);
  height: 20px;
  width: 20px;
  display: grid;
  place-content: center;
  border-radius: 3px;
  color: white;
}

.chat-container {
  display: grid;
  grid-template-rows: min-content 9fr 1fr;
  grid-column-start: 2;
  grid-row-start: 2;
}

.chat-header {
  display: flex;
  align-items: center;
  border: 1px solid rgb(226, 226, 226);
  padding: 10px;
  grid-row-start: 1;
  grid-row-end: 2;
}

.chat-header-name {
  font-size: large;
  font-weight: 700;
  margin-right: 5px;
}

.chat-history {
  grid-row-start: 2;
  grid-row-end: 3;
}

.chat-input-container {
  display: grid;
  place-items: center;
  grid-row-start: 3;
  grid-row-end: 4;
}

.chat-tools-container {
  width: 95%;
  height: 80%;
  border-radius: 10px;
  border: 1px solid rgb(0, 0, 0);
  display: flex;
  justify-content: center;
  align-items: center;
}

.chat-input {
  resize: none;
  width: 90%;
  height: 30px;
  padding: 5px;
  margin-right: 5px;
  outline: none;
  font-family: 'Lato', sans-serif;
}

.chat-icon {
  padding: 5px;
  width: 15px;
  border-radius: 4px;
}

.chat-icon:hover {
  background-color: rgb(214, 214, 214);
  cursor: pointer;
}
